<template>
    <div class="workMainWindowRoot">
        <div class="workNavContainer">
            <span>工作台</span>
        </div>
        <div class="workHeaderContainer">
            <div class="table">
                <div class="row">
                    <div class="cell">
                        <div>车牌号:</div>
                    </div>
                    <div class="cell">
                        <input v-model="carNumber" />
                    </div>
                    <div class="cell">
                        <button @click="selectCarNumber">选择</button>
                    </div>
                </div>
                <div class="row">
                    <div class="cell">
                        <div>搅拌站:</div>
                    </div>
                    <div class="cell">
                        <input v-model="startLocation" />
                    </div>
                    <div class="cell">
                        <button @click="selectStartLocation">选择</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="workBodyContainer">
            <div class="funContainer">
                <button class="btn marginbottom" @click="sayStation" :disabled="SayStation">报站</button>
                <button class="btn marginbottom" @click="cancelStation" :disabled="Rest">取消报站</button>


                <!-- 任务列表 -->
                <ul class="marginbottom taskList" :class="taskDisplayList">
                    <li>始发地:{{ currentTask.StartLocation }}</li>
                    <li>目的地:{{ currentTask.DestinatLocation }}</li>
                    <li>车次:{{ currentTask.CarCount }}</li>
                    <li>方量:{{ currentTask.RealFangliang }}</li>
                    <li>混凝土规格:{{ currentTask.type }}</li>
                </ul>


                <button class="btn" @click="acceptHandler" :disabled="Accept">接收任务</button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="loadThingHandler" :disabled="LoadThing">倒车</button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="startHandler" :disabled="Start">出站</button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="arrivalHandler" :disabled="Arrive">到工地</button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="startOffHandler" :disabled="StartOff">开始卸车</button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="offCompleteHandler" :disabled="OffCompelete">卸完</button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="backHandler" :disabled="Back">回站</button>
            </div>


        </div>
    </div>
</template>
  
<script>
const PubSub = require('pubsub-js')

export default {
    data() {
        return {
            taskDisplayList: 'noneStyle',
            currentTask: {},
            timer: null,
            UserInfo: {},
            carNumber: '',
            startLocation: '',
            SayStation: false,
            Rest: true,
            Accept: true,
            LoadThing: true,
            Start: true,
            Arrive: true,
            StartOff: true,
            OffCompelete: true,
            Back: true
        }
    },
    name: 'workerPage',
    methods: {
        acceptHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        AcceptTaskTime: this.$getCurrentTime().time,
                        state: 'accept',
                        comment: '排队倒车中'
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.LoadThing = false;
                            clearInterval(this.timer)
                            this.timer = null;
                        },
                        fail => {

                        }
                    )
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        loadThingHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        LoadThingTime: this.$getCurrentTime().time,
                        state: 'loadThing',
                        comment: '倒车中'
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.Start = false;
                        },
                        fail => {

                        }
                    )
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        clearState() {
            this.SayStation = true
            this.Rest = true
            this.Accept = true
            this.LoadThing = true
            this.Start = true
            this.Arrive = true
            this.StartOff = true
            this.OffCompelete = true
            this.Back = true
        },
        //选择车牌号
        selectCarNumber() {
            this.$router.push({
                name: 'selectionITemPage',
                query: {
                    type: 'carNumber',
                    currentValue: this.carNumber
                }
            })
        },
        //选择始发地
        selectStartLocation() {
            this.$router.push({
                name: 'selectionITemPage',
                query: {
                    type: 'startLocation',
                    currentValue: this.startLocation
                }
            })
        },
        //处理选择的回调值
        handleSelectedValue(msg, data) {
            switch (data.type) {
                case 'carNumber':
                    this.carNumber = data.value;
                    break;
                case 'startLocation':
                    this.startLocation = data.value;
                    break;
            }
        },
        //报站
        sayStation() {

            if (this.carNumber.trim() === '') {
                alert('车牌号不能为空！')
                return;
            }
            if (this.startLocation.trim() === '') {
                alert('搅拌站不能为空！')
                return;
            }


            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber
                },
                body: {
                    $set: {
                        StartLocation: this.startLocation,
                        CarNumber: this.carNumber,
                        UserName: user.UserName,
                        RealName: user.RealName,
                        sayStationTime: this.$getCurrentTime().time,
                        state: 'waitTask',
                        comment: '待命',
                        DestinatLocation: '',
                        CarCount: '',
                        RealFangliang: '',
                        type: '',
                        LoadLocation: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.Rest = false;

                            if (this.timer === null) {
                                this.checkTask()
                                this.timer = setInterval(this.checkTask, 2000);
                            }

                            alert('报站成功！')
                        },
                        fail => {

                        }
                    )

                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );


        },
        //取消报站
        cancelStation() {

            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        CarNumber: this.carNumber,
                        StartLocation: '',
                        UserName: '',
                        RealName: '',
                        state: 'rest',
                        comment: '休息',
                        DestinatLocation: '',
                        CarCount: '',
                        RealFangliang: '',
                        type: '',
                        LoadLocation: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.SayStation = false;
                            clearInterval(this.timer)
                            this.timer = null;
                            this.taskDisplayList = 'noneStyle'
                            alert('取消报站成功！')
                        },
                        fail => {

                        }
                    )
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        //出站
        startHandler() {
            let tm=this.$getCurrentTime();
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        leaveStationTime: tm.time,
                        startYear:tm.yy,
                        startMonth:tm.mm,
                        startDay:tm.dd,
                        state: 'start',
                        comment: '在去工地的路上'
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-where-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.Arrive = false;
                            break;
                        case "dataBase-error":
                            console.log("服务器端数据库报错！");
                            break;
                        case "server-undefinedError":
                            console.log("服务器端报错！");
                            break;
                    }
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        //到达工地
        arrivalHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        arrivalTime: this.$getCurrentTime().time,
                        state: 'arrival',
                        comment: '到工地,等待卸车'
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-where-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.StartOff = false;
                            break;
                        case "dataBase-error":
                            console.log("服务器端数据库报错！");
                            break;
                        case "server-undefinedError":
                            console.log("服务器端报错！");
                            break;
                    }
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        //开始卸车
        startOffHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        startOffTime: this.$getCurrentTime().time,
                        state: 'startOff',
                        comment: '卸车中'
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-where-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.OffCompelete = false;
                            break;
                        case "dataBase-error":
                            console.log("服务器端数据库报错！");
                            break;
                        case "server-undefinedError":
                            console.log("服务器端报错！");
                            break;
                    }
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        //卸完
        offCompleteHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        offCompleteTime: this.$getCurrentTime().time,
                        state: 'offComplete',
                        comment: '卸完,在回站的路上'
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-where-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "body-body-is-null":
                            console.log("服务器端报错！");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.Back = false;
                            break;
                        case "dataBase-error":
                            console.log("服务器端数据库报错！");
                            break;
                        case "server-undefinedError":
                            console.log("服务器端报错！");
                            break;
                    }
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        //回站
        backHandler() {
            //更新行车状态
            let timeObj = this.$getCurrentTime();
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        backTime: timeObj.time,
                        state: 'waitTask',
                        comment: '待命',
                        DestinatLocation: '',
                        CarCount: '',
                        RealFangliang: '',
                        type: '',
                        LoadLocation: ''
                    }
                }
            };

            //获取当前车辆状态信息
            this.getCurrentUserCarStatData().then(
                success => {
                    success.backTime = timeObj.time
                    let insertData = {
                        StartLocation: success.StartLocation,
                        CarNumber: success.CarNumber,
                        MaxFangliang: success.MaxFangliang,
                        UserName: success.UserName,
                        RealName: success.RealName,
                        sayStationTime: success.sayStationTime,
                        leaveStationTime: success.leaveStationTime,
                        startYear:success.startYear,
                        startMonth:success.startMonth,
                        startDay:success.startDay,
                        arrivalTime: success.arrivalTime,
                        startOffTime: success.startOffTime,
                        offCompleteTime: success.offCompleteTime,
                        backTime: success.backTime,
                        CarCount: success.CarCount,
                        DestinatLocation: success.DestinatLocation,
                        RealFangliang: success.RealFangliang,
                        type: success.type,
                        LoadLocation: success.LoadLocation,
                    }

                    this.$http.post("/insertTransInfo", insertData).then(
                        (scucess) => {
                            switch (scucess.data) {
                                case "handleSucess":
                                    console.log("车辆运输明细添加成功！");
                                    //然后再更新行车状态
                                    this.updateCarStateInfo(stateInfo)
                                    break;
                                case "body-is-null":
                                    console.log("body-is-null");
                                    break;
                                case "dataBase-error":
                                    console.log("服务器端数据库操作报错！");
                                    break;
                                case "server-undefinedError":
                                    console.log("服务器端报错！");
                                    break;
                            }
                        },
                        (fail) => {
                            console.log("网络请求超时！");
                        }
                    );


                },
                fail => { alert('处理失败!') }
            )



        },
        //更新行车状态
        updateCarStateInfo(stateInfo) {
            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {

                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.Rest = false;
                            this.taskDisplayList = 'noneStyle'
                            if (this.timer === null) {
                                this.checkTask()
                                this.timer = setInterval(this.checkTask, 2000);
                            }
                        },
                        fail => {

                        }
                    )

                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        getCurrentUserCarStatData() {
            return new Promise((resolve, rejects) => {
                let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
                //定义条件
                let where = {
                    UserName: user.UserName
                };
                //发送请求
                this.$http
                    .get(`/transStateQuerys?where=${JSON.stringify(where)}`)
                    .then((info) => {
                        //服务器只要接到请求就会返回200成功
                        this.$handleRequest(info.data).then(
                            success => {
                                console.log("获取车辆状态列表成功了！")
                                //如果被指派了任务，则点亮接收任务按钮，并关闭计时器
                                let db = success[0];
                                resolve(db);
                            },
                            fail => {
                                rejects();
                                console.log("获取车辆状态列表失败了！")
                            }
                        )
                    },
                        (fail) => { console.log(fail) })
                    .catch((err) => {
                        console.log(`网络请求超时！${err}`);
                    });
            })
        },
        getCarStateList() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            //定义条件
            let where = {
                UserName: user.UserName
            };
            //发送请求
            this.$http
                .get(`/transStateQuerys?where=${JSON.stringify(where)}`)
                .then((info) => {
                    //服务器只要接到请求就会返回200成功
                    this.$handleRequest(info.data).then(
                        success => {
                            console.log("获取车辆状态列表成功了！")
                            //如果被指派了任务，则点亮接收任务按钮，并关闭计时器
                            let db = success[0];
                            if (db.state === 'waitAccept') {
                                this.Rest = true;
                                this.Accept = false;
                                clearInterval(this.timer)
                                this.timer = null;
                                this.currentTask = db;
                                this.taskDisplayList = ''
                            }
                        },
                        fail => {
                            this.carStateList = [];
                            console.log("获取车辆状态列表失败了！")
                        }
                    )
                },
                    (fail) => { console.log(fail) })
                .catch((err) => {
                    console.log(`网络请求超时！${err}`);
                });
        },
        checkTask() {
            this.getCarStateList()
        }
    },
    mounted() {
        console.log('mounted')
        //第一次加载页面，初始化数据
        //先获取用户的行车状态
        //如果有，则根据行车状态初始化
        //如果没有则，进行默认初始化
        this.getCurrentUserCarStatData().then(
            success => {
                //根据用户的行车状态初始化数据
                this.clearState();
                this.carNumber = success.CarNumber;
                this.startLocation = success.StartLocation;
                switch (success.state) {
                    case "rest":
                        this.SayStation = false;
                        break;
                    case "waitTask":
                        //等待任务中，需要时刻关注是否有任务发布
                        this.Rest = false;
                        if (this.timer === null) {
                            this.checkTask()
                            this.timer = setInterval(this.checkTask, 2000);
                        }
                        break;
                    case "waitAccept":
                        this.Accept = false;
                        //获取任务列表
                        this.checkTask()
                        break;
                    case "accept":
                        this.LoadThing = false;
                        break;
                    case "loadThing":
                        this.Start = false;
                        break;
                    case "start":
                        this.Arrive = false;
                        break;
                    case "arrival":
                        this.StartOff = false;
                        break;
                    case "startOff":
                        this.OffCompelete = false;
                        break;
                    case "offComplete":
                        this.Back = false;
                        break;
                    case "back":
                        this.Rest = false;
                        break;
                }
            },
            fail => {
                //没有行车状态进行默认初始化
                let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
                this.carNumber = user.CarNumber;
                this.clearState();
                this.SayStation = false;
            })
    },
    created() {
        console.log('created')
        //订阅选择器  执行一次即可
        PubSub.subscribe('selectedValue', this.handleSelectedValue)
    },
    activated() {
        console.log('activated')
        let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
        if (user == null || user == undefined) {
            alert('还没有登录的情况!')
            return;
        }
        if (user.UserName === this.UserInfo.UserName) {
            //用户切到其他页面，又切回了此页面
            if (!this.Rest && this.Accept) {
                //已报站，等待分配任务，定时查看是否有任务
                if (this.timer === null) {
                    this.checkTask()
                    this.timer = setInterval(this.checkTask, 2000);
                }
            }
            return;
        }
        if (user.UserName != this.UserInfo.UserName) {
            //切换登录用户的情况
            this.UserInfo.UserName = user.UserName;
            this.UserInfo.Password = user.Password;
            this.UserInfo.CarNumber = user.CarNumber;
            this.UserInfo.RealName = user.RealName;
            this.UserInfo.IDNumber = user.IDNumber;

            //进行默认值设置

            //获取行车状态 并根据行车状态进行界面更新

            return;
        }
    },
    deactivated() {
        console.log('deactivated')
        if (this.timer != null) {
            clearInterval(this.timer)
            this.timer = null;
        }
    }
}

</script>
  
<style>
.marginbottom {
    margin-bottom: 5vw;
}

.workMainWindowRoot .workNavContainer {
    text-align: center;
}

.workMainWindowRoot .workHeaderContainer {
    display: flex;
    justify-content: space-around;
}

.workMainWindowRoot .workHeaderContainer .cell:first-child {
    width: 15vw;
}

.workNavContainer .cell div {
    width: 20vw;
}



.workBodyContainer {
    display: flex;
    justify-content: center;
}

.workBodyContainer .taskList {
    color: red;
}

.workBodyContainer .funContainer {
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 110vh;
}

.workBodyContainer .funContainer button {
    width: 80vw;
    line-height: 10vw;
    border: 1px solid orange;
    display: flex;
    justify-content: center;
    align-items: center;
}

.workBodyContainer .funContainer button:active {
    background-color: orange;
}

.workBodyContainer .funContainer img {
    width: 10vw;
    height: 5vh;
}
</style>
  