<template>
    <div class="selectionRoot">
        <div class="table">
            <div v-for="(item, i) in lists" class="row">
                <div class="cell">
                    <input type="radio" v-model="selectedValue" :value="item.value" name="c" />{{ item.value }}
                </div>
            </div>
            <div class="row">
                <div class="cell">
                    <div @click="btnOk">确定</div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
const PubSub = require('pubsub-js')
export default {
    name: 'selectionITemPage',
    data() {
        return {
            selectedValue: '',
            lists: []
        }
    },
    mounted() {
        let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
        //根据类型获取相映的列表
        let url = null;
        let where = {};
        switch (this.$route.query.type) {
            case 'carNumber':
                url = '/carsInfoQuery';
                break;
            case 'startLocation':
                url = '/startLocationsInfoQuery';
                break;
        }

        if (url === null || where === null) {
            return;
        }

        //alert(`${url}?where=${JSON.stringify(where)}`);
        //发送请求
        this.$http
            .get(`${url}?where=${JSON.stringify(where)}`)
            .then((info) => {
                //服务器只要接到请求就会返回200成功
                this.handleRequest(info.data);
            }, (fail) => {
                console.log(fail)
            })
            .catch((err) => {
                console.log(`网络请求超时！${err}`);
            });

        this.selectedValue = this.$route.query.currentValue;
    },
    methods: {
        btnOk() {
            switch (this.$route.query.type) {
                case 'carNumber':
                    PubSub.publish('selectedValue', {
                        type: 'carNumber',
                        value: this.selectedValue
                    });
                    break;
                case 'startLocation':
                    PubSub.publish('selectedValue', {
                        type: 'startLocation',
                        value: this.selectedValue
                    });
                    break;
            }
            this.$router.back();
        },
        handleRequest(data) {
            switch (data) {
                case "where-undefined":
                    //where条件为undefined
                    console.log("where-undefined  where条件为undefined");
                    break;
                case "data-null":
                    window.alert('没有获取到数据')
                    //根据where条件没有查找到用户数据
                    console.log("data-null 根据where条件没有查找到用户数据");
                    break;
                case "dataBase-error":
                    //服务器数据库操作报错
                    console.log("dataBase-error 服务器数据库操作报错");
                    break;
                case "server-undefinedError":
                    //服务器未知错误
                    console.log("server-undefinedError 服务器未知错误");
                    break;
                default:
                    //获取到了数据
                    this.lists = [];
                    switch (this.$route.query.type) {
                        case 'carNumber':
                            data.forEach(element => {
                                let item = {}
                                item.value = element.CarNumber;
                                this.lists.push(item)
                            });
                            break;
                        case 'startLocation':
                            data.forEach(element => {
                                let item = {}
                                item.value = element.LocationName;
                                this.lists.push(item)
                            });
                            break;
                    }
                    break;
            }
        }
    }
}
</script>
  
<style>
.selectionRoot .table {
    height: 100vh;
    align-items: center;
    font-size: 5vw;
}
</style>
  