<template>
    <div class="carCountFanliangStaticRoot">
        <div class="table">
            <div class="row">
                <div class="cell">
                    <div>姓名</div>
                </div>
                <div class="cell">
                    <div>车牌号</div>
                </div>
                <div class="cell">
                    <div>日期</div>
                </div>
                <div class="cell">
                    <div>起运地</div>
                </div>
                <div class="cell">
                    <div>目的地</div>
                </div>
                <div class="cell">
                    <div>方量</div>
                </div>
                <div class="cell">
                    <div>规格</div>
                </div>
            </div>
            <div v-for="(item, i) in list" class="row" @click="rowClickHandler(item)">
                <div class="cell">
                    <div>{{ item.RealName }}</div>
                </div>
                <div class="cell">
                    <div>{{ item.CarNumber }}</div>
                </div>
                <div class="cell">
                    <div>{{ item.leaveStationTime }}</div>
                </div>
                <div class="cell">
                    <div>{{ item.StartLocation }}</div>
                </div>
                <div class="cell">
                    <div>{{ item.DestinatLocation }}</div>
                </div>
                <div class="cell">
                    <div>{{ item.RealFangliang }}</div>
                </div>
                <div class="cell">
                    <div>{{ item.type }}</div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>

export default {
    name: 'carCountFanliangStatic',
    methods: {
        refresh() {
            let where = {
                startYear: this.$route.query.year,
                startMonth: this.$route.query.month,
                startDay: this.$route.query.day,
                UserName: this.$route.query.UserName
            };


            //发送请求
            this.$http
                .get(`/transInfoQuery?where=${JSON.stringify(where)}`)
                .then((info) => {
                    this.$handleRequest(info.data).then(
                        sucess => {
                            this.list = sucess
                        },
                        fail => {

                        }
                    )
                },
                    (fail) => {
                        console.log(fail)
                    })
                .catch((err) => {
                    console.log(`网络请求超时！${err}`);
                });
        }
    },
    data() {
        return {
            list: [],
        }
    },
    mounted() {
        this.refresh();
    }
}
</script>
  
<style>
.carCountFanliangStaticRoot .row .cell {
    flex: 1;
    justify-content: center;
}
</style>
  