<template>
  <div class="profileSettingRoot">
    <div class="table">
      <div class="row">
        <div class="cell">
          <div>用户名:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.UserName" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>密码:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.Password" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>车牌号:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.CarNumber" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>真实姓名:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.RealName" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>身份证号:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.IDNumber" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <button @click="updateProfile">修改</button>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "driverProfileSetting",
  methods: {
    selectedQueue() {},
    updateProfile() {
      let body = {
        where: this.userInfo.UserName,
        userInfo: this.UserInfo,
      };
      this.$http.post("/updateUserInfo", body).then(
        (scucess) => {
          switch (scucess.data) {
            case "where-undefined":
              console.log("服务器端报错！");
              break;
              case "userInfo-undefined":
              console.log("服务器端报错！");
              break;
            case "handleSucess":
              console.log("注册成功！");
              this.$router.push({ name: "login" });
              break;
            case "server-error":
              console.log("服务器端报错！");
              break;
          }
        },
        (fail) => {
          console.log("网络请求超时！");
        }
      );
    },
    selecteCarNumber() {},
  },
  mounted() {
    let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
    this.UserInfo.UserName = user.UserName;
    this.UserInfo.Password = user.Password;
    this.UserInfo.CarNumber = user.CarNumber;
    this.UserInfo.RealName = user.RealName;
    this.UserInfo.IDNumber = user.IDNumber;
  },
  data() {
    return {
      UserInfo: {
        UserName: "",
        Password: "",
        CarNumber: "",
        RealName: "",
        IDNumber: "",
      },
    };
  },
};
</script>
  
<style>
.profileSettingRoot .table button {
  visibility: hidden;
}

.profileSettingRoot .table .row:last-child button {
  visibility: visible;
}
.profileSettingRoot .table .row:nth-child(3) button {
  visibility: visible;
}
</style>
  