<template>
  <div class="logInRoot">
    <div class="title">
      <img src="../imgs/loginTitle.jpg" />
    </div>
    <div class="tableContainer">
      <div class="table">
        <div class="row">
          <div class="cell">
            <span>用户名:</span>
          </div>
          <div class="cell">
            <input v-model="userName" />
          </div>
        </div>
        <div class="row">
          <div class="cell">
            <span>密&#160;&#160;&#160;码:</span>
          </div>
          <div class="cell">
            <input v-model="password" />
          </div>
        </div>
        <div class="row">
          <div class="cellSpatial">
            <button @click="login">登录</button>
          </div>
          <div class="cellSpatial">
            <button @click="register">注册</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "logIn",
  data() {
    return {
      userName: "duizhang",
      password: "123",
    };
  },
  components: {},
  methods: {
    register() {
      this.$router.push({ name: "register" });
    },
    handleRequest(data) {
      switch (data) {
        case "where-undefined":
          //where条件为undefined
          console.log("where-undefined  where条件为undefined");
          break;
        case "data-null":
        window.alert('用户名或密码不正确！')
          //根据where条件没有查找到用户数据
          console.log("data-null 根据where条件没有查找到用户数据");
          break;
        case "dataBase-error":
          //服务器数据库操作报错
          console.log("dataBase-error 服务器数据库操作报错");
          break;
        case "server-undefinedError":
          //服务器未知错误
          console.log("server-undefinedError 服务器未知错误");
          break;
        default:
          //把登陆用户缓存到本地
          //window.alert('登录成功！')
          this.$router.push({ name: "driverMainWindow" });
          window.localStorage.setItem(
            this.$UserInfoKey,
            JSON.stringify(data)
          );
          break;
      }
    },
    login() {
      if (this.userName === "duizhang" && this.password === "123") {
        this.$router.push({ name: "carBossMainWindow" });
      }else if(this.userName === "jianhua" && this.password === "123"){
        this.$router.push({ name: "dispatcherWorkPage" });
      }else {
        //定义条件
        let where = {
          UserName: this.userName,
          Password: this.password,
        };
        //发送请求
        this.$http
          .get(`/userInfo?where=${JSON.stringify(where)}`)
          .then((info) => {
            //服务器只要接到请求就会返回200成功
            this.handleRequest(info.data);
          },
          (fail)=>{
            console.log(fail)
          })
          .catch((err) => {
            console.log(`网络请求超时！${err}`);
          });
      }
    },
  },
};
</script>
  
<style>
.logInRoot {
  height: 100vh;
  font-weight: 700;
  font-size: 4vw;
  /* background-image: url('../imgs/login.jpg');
    background-repeat: no-repeat;
    background-size: cover; */
}

.logInRoot img {
  width: 43vw;
}

.logInRoot .title {
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.logInRoot .tableContainer {
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.tableContainer .cell:first-child {
  width: 16vw;
}

.tableContainer .table .row:last-child {
  justify-content: space-around;
}

.tableContainer .table .row:last-child .cellSpatial {
  flex: 1;
}

.tableContainer .table .row:last-child .cellSpatial button {
  width: 30vw;
  height: 30px;
  margin: 0vw 3vw;
  background-color: red;
}
</style>
  