<template>
  <div class="registerRoot">
    <div class="table">
      <div class="row">
        <div class="cell">
          <div>用户名:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.UserName" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>密码:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.Password" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>车牌号:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.CarNumber" />
        </div>
        <div class="cell">
          <button @click="selecteCarNumber">选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>真实姓名:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.RealName" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <div>身份证号:</div>
        </div>
        <div class="cell">
          <input v-model="UserInfo.IDNumber" />
        </div>
        <div class="cell">
          <button>选择</button>
        </div>
      </div>
      <div class="row">
        <div class="cell">
          <button @click="registerDriver">注册</button>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>

const PubSub = require('pubsub-js')


export default {
  name: "workerPage",
  methods: {
    registerDriver() {
      this.$http.post("/register", this.UserInfo).then(
        (scucess) => {
          switch (scucess.data) {
            case "handleSucess":
              console.log("注册成功！");
              this.$router.push({ name: "login" });
              break;
            case "server-undefinedError":
              console.log("服务器端报错！");
              break;
          }
        },
        (fail) => {
          console.log("网络请求超时！");
        }
      );
    },
    selecteCarNumber() {
      this.$router.push({
        name: 'selectionITemPage',
        query: {
          type: 'carNumber',
          currentValue: this.carNumber
        }
      })
    },
    handleSelectedValue(msg, data) {
      console.log(data)
      switch (data.type) {
        case 'carNumber':
          this.UserInfo.CarNumber = data.value;
          break;
      }
    }
  },
  created() {
    console.log('created')
    //订阅选择器  执行一次即可
    PubSub.subscribe('selectedValue', this.handleSelectedValue)
  },
  data() {
    return {
      UserInfo: {
        UserName: "",
        Password: "",
        CarNumber: "",
        RealName: "",
        IDNumber: "",
      },
    };
  },
};
</script>
  
<style>
.registerRoot .table {
  align-items: center;
  height: 100vh;
}

.registerRoot .table button {
  visibility: hidden;
}

.registerRoot .table .row:last-child button {
  visibility: visible;
}

.registerRoot .table .row:nth-child(3) button {
  visibility: visible;
}

.registerRoot .table .row:not(.row:last-child)>.cell:first-child {
  width: 15vw;
}

.registerRoot .table .row:last-child {
  justify-content: end;
}

.registerRoot .table .row:last-child .cell button {
  width: 30vw;
  height: 7vw;
}

.registerRoot .table .row:last-child .cell {
  width: 30vw;
}
</style>
  